import { DataTypes } from "sequelize";
import sequelize from '../../middlewares/dbInstance'



const Course = sequelize.define('Course', {
  title: {
    type: DataTypes.STRING,
    allowNull: false
  }
}, {
  tableName: 'courses'
})

const Student = sequelize.define('Student', {
  name: {
    type: DataTypes.STRING,
    allowNull: false
  }
}, {
  tableName: 'students'
})

const Enroll = sequelize.define('Enroll', {
  course_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Course,
      key: 'id'
    }
  },
  student_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Student,
      key: 'id'
    }
  }
}, {
  tableName: 'students',
  timestamps: false
})


// define relations
Course.hasMany(Enroll)
Enroll.belongsTo(Course)
Enroll.belongsTo(Student)

export {
  Course, Student, Enroll
}